import React from 'react';
import { Link } from 'react-router-dom';
import StyeldComponents from 'styled-components';

import Layout from '../components/layout/layout';
import LinkBtn from '../components/ui/atoms/linkBtn';
import { quizState } from '../lib/custom/quiz';

interface ResultProps {
    statis: {
        time: number
        answer: number
        wrong: number
    };
}
const ListItem = StyeldComponents.li`
    margin: 16px 0;
`;


const Result: React.FC<ResultProps> = (props) => {
    const {
        statis
    } = props;
    const quizHook = quizState();

    return (
        <Layout header=" ">
            <ul>
                <ListItem>: {(statis.time - statis.time % 60) / 60}  {statis.time % 60} </ListItem>
                <ListItem> : {statis.answer} </ListItem>
                <ListItem> : {statis.wrong} </ListItem>
            </ul>
            <ul>
                <ListItem><LinkBtn to="/quiz" onClick={() => quizHook.selectQuiz(0)}> </LinkBtn></ListItem>
                <ListItem><LinkBtn to="/wrong"> </LinkBtn></ListItem>
            </ul>
        </Layout>
    );
};

export default Result;